@font-face {
    font-family: "arrowFt";
    src: url("../../static/arrowFont.woff") format('woff');
}

.projects {
    height: 100vh;
    // padding: 50px;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    // background-color: blue;
    .title {
        padding-top: 30px;
        display: flex;
        justify-content: space-between;
        font-size: 18pt;
        &_col2 {
            
            text-align: center;
        }
    }
    .bird {
        width: 30%;
    }
    .curl {
         width: 12%;
         align-self: flex-start;
    }
    .projectContainer {
        display: flex;
        margin-top: 30px;

        // padding-bottom: 30px;
        // justify-content: space-between;
        justify-content: space-around;        
        // background-color: yellowgreen;
        // height: 100%;
 
    }
    .a {
        display: flex;
        justify-content: flex-end;
    }
    .arrow {
        font-size: 18pt;
        margin-top: 20px;
        margin-right: 140px;
        text-align: right;
        &:hover {
            color: #ff9f63;
            cursor: pointer;
        }
    }
    .arrowFont {
        font-family: "arrowFt";
        font-size: 28pt;
        transform: rotate(90deg);
        color:  #ff9f63;
    }



    @media only screen and (max-width: 650px) {
        width: 90%;
        height: 150%;
        min-height: 650px;
        

        .title {
            // background-color: orange;
            font-size: 6vw;
            .bird {
                width: 55%;
            }
            .curl {
                 width: 15%;
                 align-self: flex-start;
            }
        }
        .projectContainer {
            flex-direction: column;
            align-items: center;
            .project {
                margin-right: auto;
                margin-left: auto;
            }
        }
    }
}
