@font-face {
    font-family: "secondaryFont";
    src: url("../../../static/samantha.woff") format('woff');
}
@font-face {
    font-family: "arrowFt";
    src: url("../../../static/arrowFont.woff") format('woff');
}
.title_desktop {
    display: flex;
    height: 100vh;
    min-height: 500px;
    flex-direction: column;
    justify-content: center;
    .innerContainer {
        display: flex;
        flex-direction: column;
        max-width: 600px;
        align-self: center;
    }
    .title {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        font-size: 22pt;
    }
    .subTitle {
        margin-top: 10px;
        font-size: 18pt;
        width: 415px;
        display: flex;
        flex-direction: column;
        margin-right: auto;
        margin-left: auto;
        h2 {
            font-family: "secondaryFont";
        }
        .line {
            margin-top: -20px;
        }
        
        &_col1 {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &_col2 {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 10px;
        }
        
    }
    .image {
        display: flex;
        align-items: flex-end;
        justify-content: space-around;
        // background-color: blue;
        // height: 50%;
        // width: 100%;
        margin-top: 10%;
        .portrait {
            // padding-bottom: 35px;
            // height:100px;
            // height: 65%;
        
        }
        .flower{
            // width: 14%;
        }

     
    }
    .arrow {
        margin-bottom: -50px;
        margin-top: 120px;
        font-size: 22pt;
        display: flex;
        &:hover {
            color: #ff9f63;
            cursor: pointer;
        }
    }
    .arrowContainer:hover {
        color: #ff9f63;
        cursor: pointer;

    }
    .arrowFont {
        font-family: "arrowFt";
        font-size: 28pt;
        transform: rotate(90deg);
        color:  #ff9f63;
    }

}
